.home-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding-left: 5vw;
    padding-right: 5vw;
    padding-top: 4vh;
    padding-bottom: 20px;
}
li{
    font-style: italic;
}
.align-row-home{
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding-top: 5vh;
}
.introduction{
    width: 100%;
    
}
.home-image{
    width: 50vw;
    height: 70vh;
}
.home-heading{
    font-size: 40px;
    font-family: "Lucida";
    
}
@media screen and (max-width:1200px) {
    .align-row-home{
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding-top: 5vh;
    }
    .home-image{
      width: 90vw;
      height: 70vh;
    }
}
@media screen and (max-width:650px) {
    .home-image{
        width: 90vw;
        height: 60vh;
        margin-left: 5vw;
        margin-right: 5vw;
    }
      .home-container{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        padding-top: 4vh;
        padding-bottom: 20px;
    }   
    .introduction{
        width: 100%;
        
        padding-left: 5vw;
    }
}
@media screen and (max-width:425px){
    .home-image{
        width: 90vw;
        height: 35vh;
        margin-left: 5vw;
        margin-right: 5vw;
    }
    .home-container{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        
        padding-top: 4vh;
        padding-bottom: 20px;
    }
    .introduction{
        width: 100%;
        
        padding-left: 5vw;
    }
}