*{
  box-sizing: border-box;
  margin: 0px;
}
.bg-container{
  display: flex;
  flex-direction: column;
  width:  100%;
  align-items: center;
  padding: 20px; 
}
.overall-background{
   width: 100vw;
}
.background{
   background-image: url("https://res.cloudinary.com/du7d2nmbw/image/upload/v1626529387/hand-painted-watercolor-background-with-sky-clouds-shape_24972-1095_xvdfql.jpg");
   min-height: 100vh;
   background-size: cover;
   width: 100%;
   
}
.todo-list-heading{
  font-size: 40px;
  margin-bottom: 14px;
  text-align: center;
  font-weight: bold;
  font-family: cursive;
}
.todo-para{
  font-size: 22px;
  margin-bottom: 10px;
  font-family: Georgia, 'Times New Roman', Times, serif;
  margin-bottom: 20px;  
}
.todo-page-container{
  display: flex;
  flex-direction: column;
  width: 90vw;
  
}
.image{
  width: 90vw;
  height: 35vh;
  flex-shrink: 1;
}
.priority-bar{
  width: 35px;
  height: 35px;
  margin-left: 10px;
  border: 1px solid black;
  padding-left: 8px;
}
.task-bar{
  width: 250px;
  height: 35px;
  margin-bottom: 10px;
  border: 1px solid black;
  padding-left: 10px;
}
.todo-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin-top: 4vh;
}
.button-add{
  width: 90px;
  height: 40px;
  font-size: 20px;
  border: 1px solid black;
  background-color: lightblue;
  margin-bottom: 4vh;
  border-radius: 12px;
  
}
.todo-list{
  display: flex;
  overflow: auto;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
  max-height: 60vh;
  width: 100%;
  
}
.diary-bg-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100vw;
  
}
.search-add-row{
  display: flex;
  flex-direction: column;
  width: 100vw;
  margin-top: 10px;
  align-items: center;
}
.search-the-date{
  background-color: transparent;
  border:1px solid black;
  text-align: center;
  width: 150px;
  display: block;
  height: 30px;
  margin-left: auto;
  margin-right: 40px;
  order: -1;
}
.days-container{
  height: 75vh;
  width: 90vw;
  display: flex;
  flex-direction: column;
  overflow: auto; 
  align-items: center; 
}
.diary-day-content{
  height: 65vh;
  width: 80vw;
  margin-top: 20px;
  overflow: auto;
  background-image: url("https://res.cloudinary.com/du7d2nmbw/image/upload/v1626532945/00f2fdd0af80ceaf17f57b6055ea4f86_czyj28.jpg");
  background-size: cover;
  padding: 2vh;
  padding-left: 42px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;

}
.today-date{
  font-size: 25px;
  font-family: cursive;
}
.delete-icon-align{
  display: flex;
  justify-content: space-between;
  width: 100%;
}
@media screen and (min-width:426px){
  .image{
    width: 90vw;
    height: 60vh;
  }
}
@media screen and (min-width:1000px){
  
  .todo-page-container{
    display: flex;
    flex-direction: row;
    width: 95vw;
    
  }
  .todo-list-heading{
      font-size: 40px;
      margin-bottom: 10px;
      text-align: center;
      font-weight: bold;
      font-family: cursive;
  }
  .todo-para{
      font-size: 22px;
      margin-bottom: 10px;
      font-family: Georgia, 'Times New Roman', Times, serif;
      margin-bottom: 20px;  
  }
  .priority-bar{
     width: 35px;
     height: 35px;
     margin-left: 10px;
     border: 1px solid black;
     padding-left: 8px;
  }
  .task-bar{
    width: 250px;
    height: 35px;
    margin-bottom: 10px;
    border: 1px solid black;
    padding-left: 10px;
  }
  .timezone{
     position: fixed;
     left: 10px;
     top: 10px;
     
  }
  
  .time{
    text-align: center;
     font-size: 20px;
  }
 
  .delete-icon{
    height: 30px;
  }
  .delete-icon-align{
     display: flex;
     justify-content: space-between;
     width: 100%;
  }

  .todo-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }
  .image{
      width: 45vw;
      height: 70vh;
      flex-shrink: 1;
  }
  .button-add{
    width: 70px;
    height: 35px;
    background-color: lightblue;
   
    border-width: 0px;
    border-radius: 12px;
    
  }
  .todo-list{
    display: flex;
    overflow: auto;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
    max-height: 40vh;
    width: 100%;
    padding-right: 10px;
    padding-left: 4vw;

  }
  .diary-bg-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100vw;
  }
  .search-the-date{
    background-color: transparent;
    border:1px solid black;
    text-align: center;
    width: 120px;
    display: block;
    height: 30px;
    margin-left: auto;
    margin-right: 20px;
    order:1;
    margin-bottom: auto;
  }
  .days-container{
     height: 70vh;
     width: 90vw;
     display: flex;
     overflow: auto;
     flex-wrap: wrap;
     justify-content: space-around;
     align-items: center; 
  }
  .diary-day-content{
    height: 65vh;
    width: 27vw;
    margin-top: 20px;
    overflow: auto;
    background-image: url("https://res.cloudinary.com/du7d2nmbw/image/upload/v1626532945/00f2fdd0af80ceaf17f57b6055ea4f86_czyj28.jpg");

    background-size: cover;
    padding: 2vh;
    padding-left: 42px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;

  }
  .today-date{
    font-size: 20px;
    font-family: cursive;
  }
  .today-day-content{
    font-family: cursive;
    font-size: 15px;
    margin-top: 5px;
  }
  .search-add-row{
    display: flex;
    flex-direction: row;
    width: 100vw;
    margin-top: 10px;
    justify-content: center;
  }
  .contact-section{
      background-color: #0d2436;
      height: 45vh;
      width: 100vw;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
  }
  .profile-section-heading{
      color: #959ead;
      font-family: "Roboto";
      margin-bottom: 5px;
      font-weight: bold;
      font-size: 26px;
      text-align: center;
  }
  .profile-section-heading1{
      color: #959ead;
      margin-bottom: 5px;
      font-family: "Roboto";
      font-weight: bold;
      font-size: 20px;
      text-align: center;
  }
  .profile-section-para{
      color: #959ead;
      font-family: "Roboto";
      font-weight: bold;
      font-size: 16px;
  }
  .mail-icon{
      color: #959ead;
      height: 16px;
  }
  .wrap-container{
      display: flex;
      flex-direction: row;
      margin-bottom: 5px;
      padding: 0px;
  }  
}