.add-the-day-container{
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    margin-left: auto;
}
.zy{
    display: flex;
    justify-content: center;
    align-items: center;
   
    margin-left: 5px;
    margin-right: 5px;
}
.add-diary{
    display: flex;
    flex-direction: column;   
    height: 400px;
    justify-content: space-around;
    align-items: center;
}
.button-add-diary{
    width: 70px;
    height: 35px;
    background-color: blanchedalmond;
    border: 1px solid black;
    border-radius: 12px;
    margin-bottom: 10px;    
}
.date-add{
    padding: 3px;
    text-align: center;
    width: 100px;
    height: 30px;
    
}
.add-day{
    height: 250px;
    width: 500px;
    padding: 10px;
}
.diary-cancel-save-button{
    display: flex;
    flex-direction: row;
    justify-content: center;
}

@media screen and (max-width:999px) {
    .add-the-day-container{
        margin-top: 10px;
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100vw;
    }
    .add-diary{
        display: flex;
        flex-direction: column;   
        height: 400px;
        justify-content: space-around;
        align-items: center;
    }
    .add-day{
        height: 250px;
        width: 400px;
        padding: 10px;
    }
    
}
@media screen and (max-width:400px) {
    .add-the-day-container{
        margin-top: 10px;
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100vw;
    }
    .add-diary{
        display: flex;
        flex-direction: column;   
        height: 400px;
        justify-content: space-around;
        align-items: center;
    }
    .add-day{
        height: 250px;
        width: 90vw;
        padding: 10px;
    }
    
}
