.todo-card{
    min-height: 70px;
    margin-bottom: 10px;
    width: 100%;
    
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    background-color: transparent;
    border: 1px solid black;
    border-radius: 13px;
    box-shadow: black;
}
.edit-button{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 8px;
    text-align: center;
}
.y{
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: center;

}
.z{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 28px;
    margin-left: 5px;
    margin-right: 5px;
}
.x{
    margin-left: 0px;
}
.task-bar1{
    width: 250px;
    height: 28px;
    margin-bottom: 10px;
    border: 1px solid black;
    padding-left: 10px;
    margin-right: 5px;
}
.task{
    font-size: 22px;
}
@media screen and (max-width:400px) {
    .task-bar1{
        width: 200px;
        height: 28px;
        margin-bottom: 10px;
        border: 1px solid black;
        padding-left: 10px;
        margin-right: 5px;
      }
}